.nav-class{
    width:100%;
    margin-top:0;
    height:80px;
    /* background-color: gray; */
    position:fixed;
}
.nav-class>ul {
    list-style: none;
}
.nav-class > img{
    height:80px;
    float:left;
}
.nav-li{
    font-family: 'Roboto', sans-serif;
    margin-right: 0.8rem;
    color:rgb(39, 9, 73);
    padding:0.8rem;
    font-size:20px;
    float: right;
    border: 0;
    font-style: bold;
    cursor: pointer;
    opacity: 0.5;
}

.nav-class ul li:hover{
    transition: opacity 0.5s bold 0.5s;
    opacity: 1;
}


body{
    overflow-x: hidden;

}
html{
        scroll-behavior: smooth;
}
@-webkit-keyframes blink {
    from,to {
        border-color: transparent
    }

    50%{
    border-color: white;
    }
}

@keyframes blink {
    from,to {
        border-color: transparent
    }

    50%{
    border-color: white;
    }
}

.home-class{
    display: flex;
    justify-content: center;
    align-items: center;
    height:100vh;
    background-image:url(/portfolio/static/media/bg.2acf3a70.jpg);
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    color: white;
    width:100%;
    opacity:1;
}

.typing-text{
    font-family: 'Noto Sans KR', sans-serif;
    border-right: 0.1em solid white;
    font-size: 3vw;
    /* white-space: pre; */
    line-height: 1;
    letter-spacing: 0.1rem;
    padding-top: 0.2em;
    -webkit-animation: blink 1s step-end infinite;
            animation: blink 1s step-end infinite;
}
.aboutme-class>h2{
    height: 300px;
    border-style: double;
    background-color: gray;
}
